.main {
  display: flex;
  height: 100%;
  width: 100%;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.main:before {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  background-image: url(../assets/image/mask.png);
}

.main > section {
  text-align: center;
  color: var(--title-color);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
